.main-container{
  position: relative;
  height: 100%;
  .slide{
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: opacity 1s;
    z-index: 50;
    & > img {
      height: 100%;
      width: 100%;
      object-fit: cover;
      object-position: center;
    }
    &.show{
      opacity: 1;
      z-index: 150;
    }
  }
  
}
.overlay-slide{
  position: absolute;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 33%;
  height: 100%;
  background-repeat: repeat;
  background-size: cover;
  z-index: 250;
  @include to(720px){
    width: 100%;
  }
  & > img{
    display: block;
    width: 250px;
    max-width: 90%;
    height: auto;
    margin: 0 auto;
    // max-width: 350px;
    opacity: 0;
    
  }
  &.bg-hospital{
    // background: $hospital-slider-bg;
    $colorBg: #1D2436;
    background-color: rgba($colorBg, $trans-desktop);
    @include to(lg){
      background-color: rgba($colorBg,$trans-mobil);
    }
  }
  &.bg-estetica{
    $colorBg: #50696B;
    background-color: rgba($colorBg, $trans-desktop);
    @include to(lg){
      background-color: rgba($colorBg,$trans-mobil);
    }
    background: $estetica-slider-bg;
    // background: lightcoral;
  }
  &.bg-tienda{
    $colorBg: #263535;
    background-color: rgba($colorBg, $trans-desktop);
    @include to(lg){
      background-color: rgba($colorBg,$trans-mobil);
    }
    // background: $tienda-slider-bg; 
    // background: lightcoral;
  }
  &.bg-quirofano{
    $colorBg: #2D2C32;
    background-color: rgba($colorBg, $trans-desktop);
    @include to(lg){
      background-color: rgba($colorBg,$trans-mobil);
    }
    // background: $quirofano-slider-bg;
    // background: lightcoral;
  }
  &.right{
    right: 0;
  }
  &.left{
    left: 0;
  }
}
.slider-navigator{
  $size: 12px;
  $color: #5596CC;
  @extend %list-style;
  display: flex;
  position: absolute;
  bottom: 1em;
  right: 2em;
  z-index: 500;
  li{
    margin-right: 5px;
    width: $size;
    height: $size;
    border-radius: 50%;
    background: #fff;
    cursor: pointer;
    transition: background .3s;
    &:hover{
      background: $color
    }
    &:last-child{
      margin-right: 0;
    }
    &.active{
      background: $color;
    }
  }
}
.overlay-slide.left{
  transform: translateX(-100%)
}
.overlay-slide.right{
  transform: translateX(100%)
}
.slide.show{
  .overlay-slide{
    animation: show 1s .5s forwards;
    // transform: translateX(0)
    @keyframes show {
      to{
        transform: translateX(0);
      }
    }
    img{
      animation: showimg .5s 1.5s forwards;
      @keyframes showimg {
        to{
          opacity: 1;
        }
      }
    }
  }
  
}
