.Footer{
  min-width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 80%;
  padding: 0 2rem;
  @include from(m){
    justify-content: flex-start;
  }
  img{
    height: 12px;
    width: auto;
  }
  p{
    font-size: 1em;
    color: #CB747A;
    margin: 0;
  }
}
