$logo-height: .68;
$icon-height: 30px;
.Header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
  position: relative;
}
.logo-link{
  height: calc(var(--header-height) * #{$logo-height});
}
.Logo{
  height: calc(var(--header-height) * #{$logo-height});;
  width: auto;
}
.links-container{
  @extend %list-style;
  display: flex;
  li{
    margin-right: .7em;
    &:nth-child(2){
      margin-right: 1em;
    }
    &:last-child{
      margin-right: 0;
    }
  }
  a{
    display: block;
    width: $icon-height;
    height: $icon-height;
    img{
      width: 100%;
      height: 100%;
    }
  }
}
.main-menu{
  position: absolute;
  width: 80%;
  max-width: 250px;
  min-height: 250px;
  display: block;
  top: var(--header-height);
  right: 0;
  background: #fff;
  padding: 1rem 0;
  transform: translateX(100%);
  transition: all .5s;
  font-size: 1rem;
  z-index: 10000;
  @include to(lg){
    transform: translateY(0);
    max-width: 0;
    overflow: hidden;
    max-height: 270px;
    // & * {
    //   display: none
    // }
  }
  &.show{
    transform: translateX(0);
    max-width: 250px;
    
  }
  ul{
    @extend %list-style;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
  }
  a{
    line-height: 3rem;
    font-size: 1.3rem;
    color: $color-main;
    display: block;
    text-align: center;
  }
}
