:root {
  --header-height: 60px;
  --footer-height: 30px;
  --color-main: #5596CC;
  --translate-video: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: sans-serif;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

body.dev:before,
body.desarrollo:before, body.dev .ed-container:before,
body.desarrollo .ed-container:before, body.dev .ed-item:before,
body.desarrollo .ed-item:before {
  content: attr(class);
  font-style: italic;
  font-size: 0.75rem;
  font-weight: normal;
  z-index: 1;
  right: 0;
  position: absolute;
  display: table;
  width: 100%;
  padding: 0 0.3125em;
  height: 1.25rem;
  line-height: 1.25rem;
}

body.dev,
body.desarrollo {
  margin: 0 !important;
  position: relative;
  top: 66px;
}

body.dev:before,
body.desarrollo:before {
  position: fixed;
  background: steelblue;
  color: #FFF;
  font-size: 0.875rem;
  text-align: center;
  line-height: 36px;
  height: 36px;
  top: 0;
}

body.dev:before,
body.desarrollo:before {
  content: "ed-grid: size s, from 0 (ed-containers: red, ed-items: blue, yellow)";
}

@media screen and (min-width: 640px) {
  body.dev:before,
  body.desarrollo:before {
    content: "ed-grid: size m, from 640px (ed-containers: red, ed-items: blue, yellow)";
  }
}

@media screen and (min-width: 1024px) {
  body.dev:before,
  body.desarrollo:before {
    content: "ed-grid: size l, from 1024px (ed-containers: red, ed-items: blue, yellow)";
  }
}

@media screen and (min-width: 1024px) {
  body.dev:before,
  body.desarrollo:before {
    content: "ed-grid: size lg, from 1024px (ed-containers: red, ed-items: blue, yellow)";
  }
}

@media screen and (min-width: 1440px) {
  body.dev:before,
  body.desarrollo:before {
    content: "ed-grid: size xl, from 1440px (ed-containers: red, ed-items: blue, yellow)";
  }
}

body.dev .ed-container,
body.desarrollo .ed-container {
  padding-top: 1.25rem;
  margin-bottom: 10px;
  outline: 1px solid tomato;
  position: relative;
}

body.dev .ed-container .container,
body.desarrollo .ed-container .container {
  margin-bottom: 0;
}

body.dev .ed-container:before,
body.desarrollo .ed-container:before {
  top: 0;
  background: rgba(255, 99, 71, 0.3);
  color: tomato;
}

body.dev .ed-item,
body.desarrollo .ed-item {
  position: relative;
  padding-bottom: 1.25rem;
  background-color: rgba(255, 255, 0, 0.3);
  background-clip: content-box;
}

body.dev .ed-item:before,
body.desarrollo .ed-item:before {
  background: rgba(70, 130, 180, 0.8);
  color: #FFF;
  bottom: 0;
}

body.dev .ed-item:nth-child(even):before,
body.desarrollo .ed-item:nth-child(even):before {
  background: rgba(70, 130, 180, 0.4);
  color: steelblue;
}

