.ed-grid {
  display: grid;
  grid-template-columns: 100%;
  grid-column-gap: var(--gap);
  width: calc(100% - 2rem);
  margin-left: 1rem;
  margin-right: 1rem;
  max-width: 1400px;
}

.ed-grid.gap-0 {
