  z-index: 500;
}

.slider-navigator li {
  margin-right: 5px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #fff;
  cursor: pointer;
  transition: background .3s;
}

.slider-navigator li:hover {
  background: #5596CC;
}

.slider-navigator li:last-child {
  margin-right: 0;
}

.slider-navigator li.active {
  background: #5596CC;
}

.overlay-slide.left {
  transform: translateX(-100%);
}

.overlay-slide.right {
  transform: translateX(100%);
}

.slide.show .overlay-slide {
  animation: show 1s .5s forwards;
}

@keyframes show {
  to {
    transform: translateX(0);
  }
}

.slide.show .overlay-slide img {
  animation: showimg .5s 1.5s forwards;
}

@keyframes showimg {
  to {
    opacity: 1;
  }
}
