$color: #fff;
#video-background {
  position: absolute;
  display: block;
  left: 0; 
  top: 0;
  max-width: 100vw; 
  min-height: 100%;
  width: 100vw; 
  height: auto;
  z-index: 10;
  transform: translateY(var(--translate-video))
}

.Main{
  position: relative;
  overflow: hidden;
}
.overlay-page{
  width: 100%;
  height: 22vh;
  position: absolute;
  z-index: 100;
  bottom: 0;
  display: flex;
  align-items: center;
  padding-left: 1em;
  padding-right: 1em;
  // @include from(m){
  //   height: 30vw;
  // }
  @include from(l){
    height: 15vw;
    max-height: 180px;
    padding-left: 2rem;
  }
  &.hospital{    
    // background: url(../../assets/img/hospital-bg.png) center / cover no-repeat;
    $colorBg: #1D2436;
    background-color: rgba($colorBg, $trans-desktop);
    @include to(lg){
      background-color: rgba($colorBg,$trans-mobil);
    }
  }
  &.estetica{
    // background: url(../../assets/img/estetica-bg.png) center / cover no-repeat;
    $colorBg: #263535;
    background-color: rgba($colorBg, $trans-desktop);
    @include to(lg){
      background-color: rgba($colorBg,$trans-mobil);
    }
  }
  img{
    max-width: 100%;
    height: auto;
    @include from(l){
      max-width: 65%;
    }
  }
  
}
.domicilio-img{
  width: 100%;
  height: auto;
  display: block;
}
.backgroud-page{
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100%;
  & .overlay-page-full{
    position: absolute;
    height: 100%;
    width: 33%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include to(720px){
      width: 100%;
    }
  }
  &.contacto{
    color: $color;
  
    background-image: url(../../assets/img/3.jpg);
    @media screen and(max-width: 1023px) and (orientation:portrait){
      background-image: url(../../assets/img/Contacto.jpg);      
    }
    & .overlay-page-full{
      // background: url(../../assets/img/ubicacion-bg.png) center / cover no-repeat;
      $colorBg: #2D2C32;
      background-color: rgba($colorBg, $trans-desktop);
      @include to(lg){
        background-color: rgba($colorBg,$trans-mobil);
      }
    }
    .overlay-content{
      position: absolute;
      width: 80%;
      max-width: 450px;
      @include to(lg){
        max-width: 70%;
      }
    }
    img{
      position: absolute;
      width: auto;
      height: 80%;
    }
    h2 {
      color: $color;
      text-align: center;
      text-transform: uppercase;
      font-family: BebasKai;
      font-size: 2.3em;
      font-weight: normal;
      letter-spacing: 2px;
      margin-bottom: 1.5rem;
      line-height: 1em;
      // transform: translateY(-.5rem);
    }
    .map{
      position: relative;
      width: 90%;
      padding-bottom: 60% ;
      margin: 0 auto 1.5em;
      overflow: hidden;
      iframe{
        @media screen and(max-width: 1023px) and (orientation:portrait){
          max-width: 180px !important;    
          display: block;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
    iframe{
      position: absolute;
      top: 0;
      left: 0;      
      border: none;
      width: 100%;
      height: 100%;
      max-width: 100%;
    }
    p{
      font-size: .8em;
      margin-top: -1.2em;
      margin-bottom: 1.5rem;
      letter-spacing: .5px;
      text-align: center;
    }
    ul{
      @extend %list-style;
      margin: 0 auto 1em;
      width: 90%;
      font-size: .9em;
      &:last-of-type{
        margin-bottom: 0;
      }

    }
  }
  &.promociones{
    background-image: url(../../assets/img/5.jpg);
    @media screen and(max-width: 1023px) and (orientation:portrait){
      background-image: url(../../assets/img/Promos.jpg);      
    }
    & .overlay-page-full{
      // background: url(../../assets/img/promos-bg.png) center / cover no-repeat;
      $colorBg: #263535;
      background-color: rgba($colorBg, $trans-desktop);
      @include to(lg){
        background-color: rgba($colorBg,$trans-mobil);
      }
    }
    .overlay-content{
      position: absolute;
      width: 363px;
      @include to(lg){
        max-width: 70%;
      }
    }
    img{
      position: absolute;
      width: 363px;
      height: 312px;
    }
    
    h2{
      color: $color;
      text-align: center;
      text-transform: uppercase;
      font-family: BebasKai;
      font-size: 2.3em;
      margin-bottom: .0;
      line-height: 1em;
      transform: translateY(-.5rem);
      span {
        margin-left: 8px;
      }
    }
    .promo-content{
      margin-top: 1.8em;
    }
    p{
      font-size: 20px;
      color: $color;
      font-weight: bold;

      &.hosp{
        margin-top: 3em;
      }
    }
    ul{
      @extend %list-style;
      li{
        color: $color;
        text-align: center;
        font-size: 20px;
      }
    }
  }
}


.opacity{
  opacity: 0;
  animation: opa 1s 1s forwards;
  
}
.scale{
  transform: scale(0);
  animation: sca .5s 2s forwards;
}

@keyframes sca {
  to{
    transform: scale(1)
  }
}


@keyframes opa {
  to {
    opacity: 1;
  }
}

.loading{
  width: 100%;
  max-width: 100vw;
  max-height: calc(100vh - var(--header-height) - var(--footer-height));
  height: calc(100vh - var(--header-height) - var(--footer-height));
  display: flex;
  justify-content: center;
  align-items: cdnter;
  position: relative;
  z-index: 1;
}

.videobg{
  position: relative;
  width: 100%;
  padding-bottom: 56.9%;
  background: transparent;
  z-index: 10;
  transform: translateY(var(--translate-video));
  iframe{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
}
main{
  position: relative;
  max-width: 100vw;
  overflow: hidden;
}
.layer{
  position: absolute;
  width: 100%;
  height: 100%;
  background: transparent;
  z-index: 20

}
.imgBackgroundPage{
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.celrot{
  widows: 100vw;
  height: 100vw;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  img{
    display: block;
    width: auto;
    height: 50px;
  }
}

#emergencyPhonemobile{
  @include from(lg){
    display: none;
  }
}
#emergencyPhone{
  @include to(lg){
    display: none;
  }
}
